@use "global" as *;

.find__inner {
  max-width: rem(966);
  width: 100%;
  margin: auto;
  padding: rem(54) $padding-pc rem(66);
  @include mq(md) {
    padding: rem(50) $padding-sp;
  }
}

.find__search {
  display: flex;
  max-width: rem(851);
  width: 100%;
  margin: rem(75) auto 0;
  @include mq(md) {
    flex-direction: column;
    row-gap: rem(18);
    margin-top: rem(40);
  }
}

.find__searchitem {
  position: relative;
  width: calc(100% / 3);
  height: rem(65);
  font-size: rem(20);
  font-weight: $normal;
  text-align: center;
  border-left: 1px solid $black;
  cursor: pointer;
  @include mq(md) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: rem(260);
    margin: auto;
    font-size: rem(14);
    height: auto;
    padding: rem(15) rem(15) rem(15) rem(34);
    text-align: left;
    border: 1px solid #ad6454;
    border-radius: rem(10);
  }
}

.find__searchitem:nth-child(2) {
  @include mq(md) {
    border: 1px solid #897624;
  }
}

.find__searchitem::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 13px 10.5px 0 10.5px;
  border-color: #ad6454 transparent transparent transparent;
  left: 50%;
  transform: translateX(-50%);
  bottom: rem(7);
  @include mq(md) {
    position: unset;
    transform: none;
  }
}

.find__searchitem:nth-child(2)::after {
  border-color: #897624 transparent transparent transparent;
}

.find__searchitem:nth-child(3) {
  border-right: 1px solid $black;
  @include mq(md) {
    border: 1px solid #863553;
  }
}

.find__searchitem:nth-child(3)::after {
  border-color: #863553 transparent transparent transparent;
}

.find__contents {
  margin-top: rem(63);
  @include mq(md) {
    margin-top: rem(40);
  }
}

.find__contents:nth-child(4) {
  margin-top: rem(84);
  @include mq(md) {
    margin-top: rem(40);
  }
}

.find__contents:nth-child(5) {
  margin-top: rem(67);
  @include mq(md) {
    margin-top: rem(40);
  }
}
// .find__contents.is-hide {
//   display: none;
// }

.find__subtitle {
  position: relative;
  padding-left: rem(51);
  letter-spacing: 1px;
  @include mq(md) {
    padding-left: 0;
    max-width: rem(210);
    width: 100%;
    margin: auto;
    text-align: center;
  }
}

.find__subtitle span {
  font-size: rem(28);
  font-weight: $bold;
  color: #CE4E30;
  background: $white;
  padding-right: rem(28);
  @include mq(md) {
    font-size: rem(24);
    padding-right: 0;
    padding-left: rem(30);
  }
}

.find__subtitle::before {
  position: absolute;
  content: "";
  width: rem(32);
  height: rem(36);
  background: url(../images/common/icon-station.svg) no-repeat;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  @include mq(md) {
    width: rem(21);
    height: rem(25);
    background-size: contain;
  }
}

.find__subtitle::after {
  position: absolute;
  content: "";
  width: 80%;
  height: rem(1);
  background: #AD6554;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  z-index: -1;
  @include mq(md) {
    display: none;
  }
}

.find__subtitle--yellow span {
  color: #B2961A;
}

.find__subtitle--yellow::before {
  background: url(../images/common/icon-purpose.svg) no-repeat;
  width: rem(27);
  height: rem(36);
  @include mq(md) {
    width: rem(22);
    height: rem(29);
    background-size: contain;
    left: rem(10);
  }
}

.find__subtitle--yellow::after {
  background: #B2961A;
}

.find__subtitle--red span {
  color: #863553;
}

.find__subtitle--red::before {
  background: url(../images/common/icon-condition.svg) no-repeat;
  width: rem(35);
  height: rem(35);
  @include mq(md) {
    width: rem(28);
    height: rem(28);
    background-size: contain;
    left: rem(10);
  }
}

.find__subtitle--red::after {
  background: #863553;
}

.find__wrap {
  display: flex;
  flex-wrap: wrap;
  gap: rem(48) rem(38);
  margin-top: rem(20);
  @include mq(md) {
    gap: rem(30) rem(15);
    justify-content: space-between;
    margin-top: rem(19);
  }
}

.find__wrap--red {
  gap: rem(21) rem(38);
  @include mq(md) {
    gap: rem(16);
  }
}

.find__item {
  max-width: rem(200);
  width: calc((100% - #{rem(38)} * 3) / 4);
  @include mq(md) {
    max-width: none;
    width: calc((100% - #{rem(16)}) / 2);
  }
}

.find__img {
  position: relative;
  padding-top: 60%;
}

.find__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border-radius: rem(10);
  object-fit: cover;
}

.find__img figcaption {
  position: absolute;
  max-width: rem(137);
  width: 100%;
  color: #AD6554;
  font-size: rem(17);
  font-weight: $regular;
  text-align: center;
  padding: rem(1) 0;
  border: 1px solid #AD6554;
  border-radius: rem(32);
  background: $white;
  left: 50%;
  transform: translateX(-50%);
  bottom: rem(-15);
  z-index: 1;
  filter: drop-shadow(0px 3px 6px rgba(0,0,0,0.16)); 
  @include mq(md) {
    max-width: rem(120);
    font-size: rem(14);
  }
}

.find__item--yellow {
  padding: rem(27) 0 rem(22);
  text-align: center;
  background: #FFFDF2;
  border: 1px solid #B2961A;
  border-radius: rem(14);
  filter: drop-shadow(0px 3px 6px rgba(0,0,0,0.16));
  @include mq(md) {
    padding: rem(19) 0;
  }
}

.find__icon {
  max-width: rem(53);
  width: 100%;
  margin: auto;
}

.find__item--yellow:nth-child(2) .find__icon {
  max-width: rem(72);
}

.find__item--yellow:nth-child(3) .find__icon {
  max-width: rem(128);
}

.find__item--yellow:nth-child(4) .find__icon {
  max-width: rem(89);
}

.find__item--yellow span {
  display: inline-block;
  color: #B2961A;
  font-size: rem(17);
  font-weight: $regular;
  margin-top: rem(22);
  @include mq(md) {
    font-size: rem(14);
    margin-top: rem(16);
  }
}

.find__item--red {
  background: #F8EFF3;
  border: 1px solid #863553;
  border-radius: rem(14);
  filter: drop-shadow(0px 3px 6px rgba(0,0,0,0.16));
  font-size: rem(17);
  font-weight: $regular;
  color: #863553;
  text-align: center;
  padding: rem(13) 0;
  @include mq(md) {
    font-size: rem(14);
  }
}

.find__btn {
  margin-top: rem(75);
  @include mq(md) {
    margin-top: rem(50);
  }
}